.statistics {
  width: 600px;
  margin: 50px auto 50px;
  box-shadow: 0 0 5px 5px #83afe136;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.title {
  display: flex;
  font-weight: 500;
  align-items: center;
  justify-content: center;
  background-color: RGBA(204, 174, 198, 0.4);
}

.list {
  display: flex;
  justify-content: space-around;
  height: 80px;
}

.label {
  font-size: 20px;
  text-shadow: 1px 1px 2px #0000005d, 0 0 1em #0000005d, 0 0 0.2em #0000005d;
}

.percentage {
  font-size: 20px;
  text-shadow: 1px 1px 2px #0000005d, 0 0 1em #0000005d, 0 0 0.2em #0000005d;
}
